nt: center;\n  align-items: flex-start;\n}\n\n/*  mode menu  */\n\n.mode-menu {\n  display: flex;\n  padding: 20px;\n  width: 30%;\n  margin-top: 5%;\n  align-items: center;\n  border-radius: 10px;\n  flex-direction: column;\n  color: rgb(49, 73, 104);\n  background-color: rgba(255, 255, 255, 0.6);\n  border: 2px solid rgba(255, 255, 255, 0.3);\n  box-shadow: 0 0 20px rgb(212 231 255);\n  min-width: 250px;\n  min-height: 250px;\n}\n\n.mode-options {\n  display: flex;\n  gap: 30px;\n  width: 100%;\n  flex-direction: column;\n}\n\n.mode-options button {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  font-family: monospace, \"Courier New\";\n  color: rgb(49, 73, 104);\n  font-size: 16px;\n  padding: 15px 30px;\n  border-radius: 12px;\n  transform: translateY(0);\n  transition: all 0.3s ease-in-out;\n  background-color: rgba(255, 255, 255, 0.5);\n  border: 2px solid rgba(255, 255, 255, 0.5);\n  box-shadow: 0 4px 20px rgba(255, 255, 255, 0.7);\n}\n\n.icon {\n  display: inline-block;\n  width: 20px;\n  height: 20px;\n  background-size: contain;\n  background-repeat: no-repeat;\n}\n\n.person-icon {\n  background-image: url(\"./images/human.png\");\n}\n\n.robot-icon {\n  background-image: url(\"./images/robot.png\");\n  width: 30px;\n  height: 30px;\n}\n\n.mode-options button:hover {\n  border: 2px solid rgba(255, 255, 255, 0);\n  transform: translateY(-5px);\n  background-color: rgb(192, 219, 255);\n  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.473);\n}\n\n.mode-options button:focus {\n  outline: none;\n  box-shadow: 0 8px 30px rgb(255, 255, 255);\n}\n\n/* game over modal */\n\n.gameover-modal {\n  display: flex;\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  padding: 20px;\n  width: 20%;\n  min-width: 200px;\n  margin-top: 5%;\n  align-items: center;\n  border-radius: 10px;\n  flex-direction: column;\n  color: rgb(147, 103, 103);\n  background-color: rgb(104 18 18);\n  border: 2px solid rgba(255, 255, 255, 0.3);\n  box-shadow: 0 0 20px rgb(128, 0, 0);\n  z-index: 1000;\n}\n\n.gameover-backdrop {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.5);\n  z-index: 999;\n}\n\n.gameover-options {\n  display: flex;\n  gap: 30px;\n  width: 100%;\n  flex-direction: column;\n}\n\n.gameover-options button {\n  color: rgb(89, 71, 71);\n  font-weight: bolder;\n  font-size: 16px;\n  padding: 15px 30px;\n  border-radius: 12px;\n  transform: translateY(0);\n  transition: all 0.3s ease-in-out;\n  font-family: monospace, \"Courier New\";\n  background-color: rgba(255, 255, 255, 0.5);\n  border: 2px solid rgba(255, 255, 255, 0.5);\n  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.473);\n}\n\n.gameover-options button:hover {\n  transform: translateY(-5px);\n  background-color: rgba(255, 255, 255, 0.698);\n  box-shadow: 0 4px 20px rgba(255, 255, 255, 0.7);\n  color: rgb(75, 75, 75);\n}\n\n/* board */\n.board-grid-container {\n  position: relative;\n  padding: 25px 0 0 25px;\n  width: fit-content;\n}\n\n.grid {\n  display: grid;\n  grid-template-columns: repeat(10, 40px);\n  grid-template-rows: repeat(10, 40px);\n  gap: 2px;\n  background-color: #1a1a1a;\n  padding: 2px;\n  border-radius: 4px;\n}\n\n.cell {\n  background-color: #ffffff;\n  border-radius: 2px;\n  transition: background-color 0.2s ease;\n}\n\n.cell:hover {\n  background-color: #aaaaaa;\n  cursor: pointer;\n}\n\n.column-labels,\n.row-labels {\n  position: absolute;\n  display: flex;\n  gap: 2px;\n}\n\n.column-labels {\n  top: 0;\n  left: 25px;\n  width: calc(100% - 25px);\n}\n\n.column-labels .label {\n  width: 40px;\n  text-align: center;\n}\n\n.row-labels {\n  top: 25px;\n  left: 0;\n  flex-direction: column;\n  height: calc(100% - 25px);\n}\n\n.row-labels .label {\n  height: 40px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.label {\n  color: #666;\n  font-size: 14px;\n  font-weight: 500;\n}\n\n.cell.ship {\n  background-color: #8dc3ee;\n}\n\n.cell.hit {\n  background-color: #ffdc52;\n}\n\n.cell.miss {\n  background-color: #303a49;\n}\n\n.cell.locked {\n  background-color: #7593b6;\n}\n\n.cell.hover {\n  background-color: rgba(33, 150, 243, 0.3);\n}\n\n/* game ui layout */\n\n.game-layout {\n  display: grid;\n  width: 95%;\n  height: 95%;\n  border-radius: 10px;\n  grid-template-rows: minmax(70px, auto) 1fr;\n  grid-template-columns: minmax(700px, 1fr) minmax(700px, 1fr);\n  grid-template-areas:\n    \"controls controls\"\n    \"playerOne playerTwo\";\n}\n\n.player-section {\n  background-color: rgba(0, 0, 0, 0.667);\n  border: 1px solid rgb(255, 255, 145);\n}\n\n.player-one-section {\n  grid-area: playerOne;\n  position: relative;\n  border-radius: 10px 0 0 10px;\n  box-shadow: -5px 0 10px rgba(255, 247, 174, 0.738),\n    0 0 10px rgba(255, 247, 174, 0.738), 0 5px 10px rgba(255, 247, 174, 0.738);\n}\n\n.player-two-section {\n  grid-area: playerTwo;\n  position: relative;\n  border-radius: 1px 10px 10px 1px;\n  box-shadow: 5px 0 10px rgba(255, 247, 174, 0.738),\n    3px 0 10px rgba(255, 247, 174, 0.738),\n    2px 5px 10px rgba(255, 247, 174, 0.738);\n}\n\n.player-one-container {\n  display: grid;\n  grid-template-rows: minmax(500px, auto) minmax(100px, auto);\n  grid-template-columns: 2fr minmax(200px, 1fr);\n  grid-template-areas: \"boardOne shipInfoOne\" \"messageOne shipInfoOne\";\n}\n\n.player-two-container {\n  display: grid;\n  grid-template-rows: minmax(500px, auto) minmax(100px, auto);\n  grid-template-columns: minmax(200px, 1fr) 2fr;\n  grid-template-areas: \"shipInfoTwo boardTwo\" \"shipInfoTwo messageTwo\";\n}\n\n.board-one {\n  grid-area: boardOne;\n  padding: 15px;\n}\n\n.ship-info-one {\n  grid-area: shipInfoOne;\n}\n\n.message-board-one {\n  grid-area: messageOne;\n  color: white;\n}\n\n.board-two {\n  grid-area: boardTwo;\n  padding: 15px;\n}\n\n.ship-info-two {\n  grid-area: shipInfoTwo;\n}\n\n.message-board-two {\n  grid-area: messageTwo;\n  color: white;\n}\n\n.turn-overlay {